import "./App.css";
import { Route, Switch, BrowserRouter as Router } from "react-router-dom";

import ScrollToTop from "./components/Scroller";
import TextEditor from "./pages/TextEditor";
import LandingPage from "./pages/LandingPage";
import About from "./pages/About";

function App() {
  return (
    <div className="App">
      <Router>
        <ScrollToTop />
        <Switch>
          <Route path="/texteditor-mintbean/editor" component={TextEditor} />
          <Route path="/texteditor-mintbean/about" component={About} />
          <Route path="/texteditor-mintbean/" component={LandingPage} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
